.visits {
  @include ul-reset;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  // min-width: 260px;

  @media (max-width: $tablet-width-only) {
    width: 100%;
    justify-content: space-between;
  }

  @media (max-width: $mobile-width-only) {
    width: 100%;
    display: grid;
    grid-row-gap: 32px;
    grid-column-gap: 29px;
  }
}

.visits .visits__item:nth-child(3n + 3) {
  @media (min-width: $desktop-width) {
    margin-right: 0;
  }
}

.visits__item {
  position: relative;
  margin-right: 60px;
  margin-bottom: 94px;

  @media (max-width: $tablet-width-only) {
    margin: 0;
    margin-bottom: 20px;
    text-align: center;
  }

  @media (max-width: $mobile-width-only) {
    margin: 0;
    text-align: center;
  }
}

.visits__item-link {
  text-decoration: none;
}

.visits__text {
  @include text-country;
  width: 61.43%;
  position: absolute;
  bottom: 0px;
  left: 70px;
  color: $dark;
  background-color: $white;
  text-align: center;
  padding: 18px 10px;

  @media (max-width: $tablet-width-only) {
    width: 74.65%;
    text-align: center;
    left: 15%;
  }

  @media (max-width: $mobile-width-only) {
    width: 75.52%;
    text-align: center;
    left: 36px;
  }
}
